.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
  margin-bottom: 20px;
}

.app-container {
  display: flex;
  flex-direction: row;
  height: calc(100vh - 100px); /* Adjust based on header height */
}

.chat-panel-container {
  width: 30%;
  border-right: 1px solid #ccc;
  padding: 10px;
  overflow-y: auto;
  background-color: #f9f9f9;
}

.browser-view-container {
  width: 70%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  background-color: #e9e9e9;
}

/* Placeholder styles for components */
.chat-panel-container p,
.browser-view-container p {
  font-style: italic;
  color: #777;
}

